/* General app container */
.app-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

/* Header styling */
.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.app-header h1 {
  margin: 0;
  font-size: 1.8rem;
  color: #333;
}

/* Logout button */
.logout-button {
  background-color: #d9534f;
  border: none;
  color: white;
  padding: 8px 14px;
  font-size: 1rem;
  border-radius: 4px;
  cursor: pointer;
}

.logout-button:hover {
  background-color: #c9302c;
}

/* Auth container for login/register */
.auth-container {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 6px;
  background-color: #f9f9f9;
  font-family: Arial, sans-serif;
}

/* Form elements */
form div {
  margin-bottom: 15px;
}

label {
  font-weight: bold;
  display: block;
  margin-bottom: 5px;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="date"],
select,
textarea {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  background-color: #0275d8;
  border: none;
  color: white;
  padding: 10px 16px;
  font-size: 1rem;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #025aa5;
}

/* Error message */
p[style*="color:red"] {
  color: #d9534f;
  font-weight: bold;
}

/* Event list */
ul {
  list-style-type: none;
  padding: 0;
}

li {
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

li a {
  font-weight: bold;
  color: #0275d8;
  text-decoration: none;
}

li a:hover {
  text-decoration: underline;
}

li button {
  margin-left: 10px;
  background-color: #5bc0de;
}

li button:hover {
  background-color: #31b0d5;
}

/* Search input */
input[type="text"][placeholder="Search events..."] {
  margin-bottom: 15px;
  padding: 8px;
  font-size: 1rem;
}

/* Event details modal */
div[style*="position: fixed"] {
  z-index: 1000;
}

div[style*="background-color: white"] {
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0,0,0,0.3);
}

/* Image in event details */
img {
  max-width: 100%;
  margin-top: 10px;
}
